.has-inline-icon {
  display: inline-flex;
  align-items: center;

  > *:nth-child(2) {
    margin-left: var(--space-xxxs);
  }
}

.is-marginless {
  margin: 0 !important;
}

.is-paddingless {
  padding: 0 !important;
}

.is-radiusless {
  border-radius: 0 !important;
}

.is-shadowless {
  box-shadow: none !important;
}

.is-unselectable {
  @extend %unselectable;
}

.is-contained {
  object-fit: contain !important;
}

.is-covered {
  object-fit: cover !important;
}

.is-full-bleed {
  width: 100vw;
  margin-left: calc(50% - 50vw);
}

